export default {
	data(){
		return{
			pwd:''
		}
	},
	props:['show',"phone"],
	methods:{
		// 关闭
		handleClose(){
			this.$emit('close',false)
			this.pwd = ''
		},
	
		// 提交
		onSubmit(){
			if(this.pwd){
				this.$emit('pwd',this.pwd)
			}else{
				uni.showToast({
					icon:'none',
					title:'请输入密码！'
				})
			}
		}
	},
	watch:{
		show(){
			if(this.show){
				this.pwd = ''
			}
		}
	}
}
